/**
 *
 * ExternalScriptManager
 *
 */

import React from 'react';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import {
  DragDropContext,
  Droppable,
  Draggable,
  DropResult,
} from 'react-beautiful-dnd';

import { Form, FormGroup, InputGroup, Col, Input, Row } from 'reactstrap';
import messages from './messages';

import DebouncedInput from '../CbDebouncedInput';
import {
  IExternalResource,
  IBaseExternalResource,
} from 'cb-utils/externalResourceUtils';
import HelpMe from '../HelpPrimitive';
import { SettingTypes, CBEvent } from 'utils/types';
import { listItemClassName } from '../PortalDevConsoleListItem';
import createFakeEvent from 'cb-utils/createFakeEvent';
import withStyledProps from 'cb-utils/withStyledProps';
import {
  ActionButtonIconWrapper,
  DevConsoleChildListItem,
  PortalDevConsoleZeroState,
} from 'containers/PortalDevConsoleSections/utils';
import { TrashIcon, PlusIcon } from '../Icons';
import { headerColor } from '../PortalDevConsoleSideBar/utils';
import sectionMessages from 'containers/PortalDevConsoleSections/messages';
import { ExternalResourceIcon } from 'containers/PortalDevConsoleSections/sections/external-resource/utils';

const InputFormGroup = styled(FormGroup)`
  margin-left: 1rem;
  margin-right: 1rem;
`;

const UrlContainer = styled(Col)`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  && {
    padding-left: 0;
  }
`;

const ListItemWrapper = withStyledProps<{ allowReorder: boolean }>()(
  styled(DevConsoleChildListItem),
)`
.${listItemClassName} {
  cursor: ${(props) => (props.allowReorder ? 'move' : 'pointer')}
}
`;

const FormGroupWrapper = styled(FormGroup)`
  && {
    margin-bottom: 0;
  }
  color: ${headerColor};
`;

const formatLinkText = (url: string) => {
  if (url.match(/\//)) {
    return url.split('/').pop();
  }
  return url;
};

export interface IExternalScriptManagerBaseProps {
  scripts: IExternalResource[];
  shouldShowRemove: boolean;
  allowReorder?: boolean; // reordering should be turned off when users are viewing external resources from an individual widgets settings
  helpMain: FormattedMessage.MessageDescriptor;
  helpNote?: FormattedMessage.MessageDescriptor;
}

export interface IProps extends IExternalScriptManagerBaseProps {
  removeScript: (i: number) => void;
  addScript: (s: IBaseExternalResource) => void;
  toggleShouldBlock: (i: number, val: boolean) => void;
  changeIndex: (i: number, val: number) => void;
  getRef?: (ref: ExternalScriptManager) => void;
}

interface IState {
  newUrl: string;
  isBlockWidgetHelpTooltipOpen: boolean;
  showInputField: boolean;
}

class ExternalScriptManager extends React.Component<IProps, IState> {
  static defaultProps: Partial<IProps> = {
    allowReorder: false,
  };

  state: IState = {
    newUrl: '',
    isBlockWidgetHelpTooltipOpen: false,
    showInputField: false,
  };

  toggleInputField = (showInputField: boolean) =>
    this.setState({ showInputField });

  showInputField = () => {
    this.toggleInputField(true);
  };

  componentDidMount() {
    if (this.props.getRef) {
      this.props.getRef(this);
    }
  }

  render() {
    const { helpMain, scripts, helpNote } = this.props;
    return (
      <Form onSubmit={this._addScript} data-cy="external-resource-form">
        {scripts.length > 0 ? (
          <React.Fragment>
            <FormGroupWrapper row className="mx-0">
              <Col sm={7} className={'text-center'}>
                <FormattedMessage {...messages.uri} />
              </Col>
              <Col sm={4} className={'text-center'}>
                <FormattedMessage {...messages.block} /> &nbsp;
                <HelpMe
                  headerText={messages.helpHeader}
                  body={
                    <div>
                      <div>
                        <FormattedMessage {...helpMain} />{' '}
                      </div>
                      <br />
                      {helpNote && (
                        <div>
                          <i>
                            <FormattedMessage {...helpNote} />{' '}
                          </i>
                        </div>
                      )}
                    </div>
                  }
                />
              </Col>
            </FormGroupWrapper>

            <DragDropContext onDragEnd={this.onDragEnd}>
              <Droppable droppableId="external-resource-list">
                {(provided) => (
                  <div ref={provided.innerRef} {...provided.droppableProps}>
                    {this.props.scripts.map(this._renderIndex)}
                  </div>
                )}
              </Droppable>
            </DragDropContext>
          </React.Fragment>
        ) : (
          <PortalDevConsoleZeroState
            title={sectionMessages.externalResources}
            icon={ExternalResourceIcon}
            info={messages.externalScriptsInfo}
            moreInfo={
              <FormattedMessage {...messages.externalScriptsMoreInfo} />
            }
            onAdd={this.showInputField}
            addButtonContents={
              <React.Fragment>
                <FormattedMessage {...messages.addResource} />
                <PlusIcon />
              </React.Fragment>
            }
          />
        )}
        {this.state.showInputField && (
          <InputFormGroup>
            <InputGroup>
              <DebouncedInput
                data-cy="external-resource-uri-input"
                type={SettingTypes.TEXT_TYPE}
                value={this.state.newUrl}
                onChange={this.handleUrlChange.bind(this)}
                placeholder={'Add JavaScript/CSS URI'}
                autoFocus
                onKeyUp={this.onInputKeyUp}
                onBlur={() => this.toggleInputField(false)}
              />
            </InputGroup>
          </InputFormGroup>
        )}
      </Form>
    );
  }

  onInputKeyUp = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.keyCode === 27) {
      // reset the input value state and hide it when users hit the escape key
      this.handleUrlChange(createFakeEvent(''));
      this.toggleInputField(false);
    }
  };

  _toggleTooltip = () => {
    this.setState((prevState) => ({
      isBlockWidgetHelpTooltipOpen: !prevState.isBlockWidgetHelpTooltipOpen,
    }));
  };

  handleUrlChange(e: CBEvent) {
    this.setState({
      newUrl: e.target.value,
    });
  }

  _toggleShouldBlock = (
    idx: number,
    e: React.ChangeEvent<HTMLInputElement>,
  ) => {
    this.props.toggleShouldBlock(idx, e.target.checked);
  };

  _changeIndex = (idx: number, newVal: number) => {
    this.props.changeIndex(idx, newVal);
  };

  _addScript = (e: React.FormEvent<HTMLInputElement | HTMLFormElement>) => {
    e.preventDefault();
    if (this.state.newUrl) {
      this.props.addScript({
        url: this.state.newUrl,
      });
      this.setState(() => ({
        newUrl: '',
        showInputField: false,
      }));
    }
  };

  onDragEnd = (result: DropResult) => {
    if (result.destination) {
      this._changeIndex(result.source.index, result.destination.index);
    }
  };

  _renderIndex = (script: IExternalResource, idx: number) => {
    return (
      <Draggable
        draggableId={script.url}
        index={idx}
        key={script.url}
        isDragDisabled={!this.props.allowReorder}
      >
        {(provided) => (
          <div
            data-cy="draggable-external-resource"
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            <ListItemWrapper
              dense
              allowReorder={this.props.allowReorder}
              content={
                <Row className="mx-0">
                  <UrlContainer sm={8}>
                    <a href={script.url} target={'_blank'}>
                      {formatLinkText(script.url)}
                    </a>
                  </UrlContainer>
                  <Col sm={3} className={'text-center'}>
                    <Input
                      data-cy="toggle-block"
                      type="checkbox"
                      checked={script.shouldBlockAllWidgets}
                      onChange={this._toggleShouldBlock.bind(this, idx)}
                    />
                  </Col>
                </Row>
              }
              action={
                this.props.shouldShowRemove && (
                  <ActionButtonIconWrapper
                    data-cy="delete-external-resource"
                    onClick={this.props.removeScript.bind(this, idx)}
                  >
                    <TrashIcon />
                  </ActionButtonIconWrapper>
                )
              }
            />
          </div>
        )}
      </Draggable>
    );
  };
}

export default ExternalScriptManager;



